<template>
  <div class="home">
    <el-form
      :model="ruleForm"
      label-position="top"
      ref="ruleForm"
      class="demo-ruleForm"
    >
      <el-form-item :label="ruleForm.resource.key">
        <el-radio-group v-model="ruleForm.resource.value">
          <el-radio label="有"></el-radio>
          <el-radio label="无"></el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item :label="ruleForm.gettime.key">
        <el-date-picker
          type="date"
          placeholder="选择患病时间"
          v-model="ruleForm.gettime.value"
          style="width: 50%"
          format="yyyy 年 MM 月 dd 日"
          value-format="yyyy-MM-dd"
        >
          ></el-date-picker
        >
      </el-form-item>
      <el-form-item :label="ruleForm.reason.key">
        <el-radio-group v-model="ruleForm.reason.value">
          <el-radio label="发作性"></el-radio>
          <el-radio label="周期性"></el-radio>
          <el-radio label="循环性"></el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item :label="ruleForm.more.key">
        <el-radio-group v-model="ruleForm.more.value">
          <el-radio label="有"></el-radio>
          <el-radio label="无"></el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item :label="ruleForm.smell.key">
        <el-radio-group v-model="ruleForm.smell.value">
          <el-radio label="药物过敏性疾患"></el-radio>
          <el-radio label="药物依赖或药瘾"></el-radio>
          <el-radio label="以上均无"></el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item :label="ruleForm.types.key">
        <el-checkbox-group v-model="ruleForm.types.value">
          <el-checkbox label="遭下放" name="types"></el-checkbox>
          <el-checkbox label="父母离异" name="types"></el-checkbox>
          <el-checkbox label="经济破产 " name="types"></el-checkbox>
          <el-checkbox label="灾变" name="types"></el-checkbox>
          <el-checkbox label="亲人死亡" name="types"></el-checkbox>
          <el-checkbox label="入狱" name="types"></el-checkbox>
          <el-checkbox label="遭强奸 " name="types"></el-checkbox>
          <el-checkbox label="外伤 " name="types"></el-checkbox>
          <el-checkbox label="车祸致残 " name="types"></el-checkbox>
          <el-checkbox label="以上均无 " name="types"></el-checkbox>
        </el-checkbox-group>
      </el-form-item>

      <el-form-item :label="ruleForm.bloodcolor.key">
        <el-radio-group v-model="ruleForm.bloodcolor.value">
          <el-radio label="有"></el-radio>
          <el-radio label="无"></el-radio>
        </el-radio-group>
      </el-form-item>

      <el-form-item style="text-align: center">
        <el-button type="primary" @click="submitForm('ruleForm')"
          >打印</el-button
        >
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
// @ is an alias to /src

export default {
  data() {
    return {
      ruleForm: {
        resource: {
          key: "1.有无精神科病史",
          value: "",
        },
        gettime: {
          key: "2.患病时间",
          value: "",
        },
        reason: {
          key: "3.发病的频率",
          value: "",
        },
        more: {
          key: "4.多次发病、多次住院患者，发作间歇期有无残留症状",
          value: "",
        },

        smell: {
          key: "5.有无以下药物过敏史",
          value: "",
        },
        types: {
          key: "6.是否有童年不良遭遇（注：童年不良遭遇指家庭环境、经济等非正常变化）（可多选）",
          value: [],
        },
        bloodcolor: {
          key: "7.是否有家系精神病史（注：包括癫痫及精神发育迟滞患者）",
          value: "",
        },
      },
    };
  },
  methods: {
    submitForm() {
      console.log(this.ruleForm);
      let symptomList = {};
      for (const key in this.ruleForm) {
        symptomList[this.ruleForm[key].key] = this.ruleForm[key].value;
      }
      window.android.printer(JSON.stringify(symptomList));
    },
  },
};
</script>
